<template>
	<div class="dataAnalysis">
		<div class="dataAnalysis_top">
			<div class="dataAnalysis_title">
				数据分析
			</div>
		</div>

		<div class="dataAnalysis_in">
			<!-- 时间： -->
			<!-- <DatePicker type="date" :value="startTime" format="yyyy-MM-dd" @on-change="getStartTime" placeholder="开始时间"
				style="width: 200px"></DatePicker> -->
				<!-- <span>-</span> -->
			<DatePicker type="daterange" :value="endTime" format="yyyy-MM-dd" @on-change="getEndTime" placeholder="请选择时间"
				style="width: 200px"></DatePicker>
			<Button type="primary" @click="handStatisticalData" class="mgleft_15">搜索</Button>
		</div>
		<div class="dataAnalysis_bot">
			<Table :columns="columns1" :data="data1" ref="tableFams" :height="tableHeight">
				<template slot-scope="{ row, index }" slot="action">
					<Button type="primary" size="small" style="margin-right: 5px" @click="show(index)">个人信息</Button>
					<Button type="primary" size="small" style="margin-right: 5px" @click="show(index)">聊天记录</Button>
					<Button type="primary" size="small" style="margin-right: 5px" @click="show(index)">备注</Button>
				</template>
			</Table>
		</div>

		<div style="text-align: center; margin-top: 20px;padding-bottom: 20px;">
			<Page :total="total" :page-size="limit" show-total  show-elevator
				@on-page-size-change="handleSizeChange" @on-change='handleCurrentChange' />
		</div>

	</div>
</template>

<script>
	import {
		indexApiModules
	} from '@/api';
	export default {
		data() {
			return {
				startTime: "",
				endTime: "",

				columns1: [{
						title: '排序',
						type: 'index',
						align: 'center'
					},
					{
						title: '日期',
						key: 'dateTime',
						align: 'dateTime'
					},
					{
						title: '接入量',
						key: 'subscribeCount',
						align: 'center'
					},
					{
						title: '客资量',
						key: 'customerCount',
						align: 'center'
					},
					// {
					// 	title: '开口率',
					// 	key: 'age2',
					// 	align: 'center'
					// },
					// {
					// 	title: '客资率',
					// 	key: 'age3',
					// 	align: 'center'
					// },
				],
				data1: [],
				wxAppId: localStorage.getItem('wxAppId'),

				page: 1, //页码
				limit: 15, // 每页的数量,
				total: 0,
				tableHeight: 607,
			}
		},
		mounted() {
			window.addEventListener('resize', this.getTableHeight);
			this.getTableHeight();
			
			this.handStatisticalData()
		},
		methods: {
			getTableHeight(){
				console.log(this.$refs.tableFams);
				if(!this.$refs.tableFams){ return}
				this.tableHeight = window.innerHeight - this.$refs.tableFams.$el.offsetTop - 200;
			},
			handStatisticalData() {
				console.log(this.endTime);
				const data = {
					page: this.page,
					limit: this.limit,
					wxAppId: this.wxAppId,
					// startTime: this.startTime,
					startTime: this.endTime[0] ? this.endTime[0] : '',
					endTime: this.endTime[1] ?  this.endTime[1] : '',
				}
				indexApiModules.statisticalData(data).then(res => {
					this.data1 = res.data.list
					this.total = res.data.pageData.count
					console.log('this.data1', this.data1)
				})
			},
			getStartTime(val) {
				this.startTime = val
			},
			// 选择时间
			getEndTime(val) {
				this.endTime = val
			},
			// 当前页
			handleCurrentChange(val) {
				this.page = val
				this.handStatisticalData()
			},
			//每页数
			handleSizeChange(val) {
				this.limit = val;
				this.page = 1;
				this.handStatisticalData()
			},
		},
	};
</script>
<style lang="less" scoped>
	// primary按钮的颜色
	.ivu-btn-primary{
		background-color: #2d8cf0;
		border-color: #2d8cf0;
	}
	.dataAnalysis {
		font-size: 16px;
		color: #333333;
		.dataAnalysis_top {
			display: flex;
			justify-content: space-between;
			align-items: center;
			padding: 14px 16px;
			border-bottom: 1px solid #DDDDDD;

			.dataAnalysis_title {
				display: flex;
				align-items: center;


				.dataAnalysis_title_item {
					margin-right: 80px;
					color: #333333;
					font-size: 16px;
				}

				.color-blue {
					color: #2d8cf0;
				}
			}
		}

		.dataAnalysis_in {
			color: #333333;
			padding: 14px 16px;
			span{
				padding: 0 10px;
			}
		}

	}
</style>
